async function load({ locals, params }) {
  if (redirects[params.slug]) {
    throw redirect(301, redirects[params.slug]);
  }
  const user = await get("user/detail?username=" + params.slug, locals.auth?.token);
  if (user.code !== 200) {
    throw error(404);
  }
  const prices = await get("price/" + user.result.uuid, locals.auth?.token);
  const locations = await get("location/" + user.result.uuid, locals.auth?.token);
  const comments = await get("comment/" + user.result.uuid, locals.auth?.token);
  return {
    user: user.result,
    prices: prices.result,
    locations: locations.result,
    comments: comments.result
  };
}
